.player {
  position: relative;
  
  .canvas {
    display: flex;
    border-radius: 6px;
    border: 4px solid black;
    width: 100%;
    height: 300px;
    overflow: hidden;

    canvas {
      width: 50% !important;
      height: 100% !important;
      object-fit: cover;
    }
  }

  .controls {
    position: absolute;
    top: 20px;
    right: 20px;
    background: rgba(0, 0, 0, 0.8);
    padding: 12px;
    border-radius: 8px;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    min-width: 200px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    transform: translateY(-10px);

    &.show {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }

    .microphoneControls {
      display: flex;
      flex-direction: column;
      gap: 8px;

      .microphoneBtn {
        padding: 8px 16px;
        background: #007bff;
        color: white;
        border: none;
        border-radius: 6px;
        cursor: pointer;
        font-size: 12px;
        font-weight: 500;
        transition: all 0.2s ease;
        white-space: nowrap;

        &:hover {
          background: #0056b3;
        }

        &.active {
          background: #dc3545;

          &:hover {
            background: #c82333;
          }
        }
      }

      .fullscreenBtn {
        padding: 8px 16px;
        background: #28a745;
        color: white;
        border: none;
        border-radius: 6px;
        cursor: pointer;
        font-size: 12px;
        font-weight: 500;
        transition: all 0.2s ease;
        white-space: nowrap;

        &:hover {
          background: #218838;
        }

        &.active {
          background: #6c757d;

          &:hover {
            background: #5a6268;
          }
        }
      }

      .trueFullscreenBtn {
        padding: 8px 16px;
        background: #fd7e14;
        color: white;
        border: none;
        border-radius: 6px;
        cursor: pointer;
        font-size: 12px;
        font-weight: 500;
        transition: all 0.2s ease;
        white-space: nowrap;

        &:hover {
          background: #e8690b;
        }

        &.active {
          background: #6c757d;

          &:hover {
            background: #5a6268;
          }
        }
      }

      .settingsBtn {
        padding: 8px 16px;
        background: #6f42c1;
        color: white;
        border: none;
        border-radius: 6px;
        cursor: pointer;
        font-size: 12px;
        font-weight: 500;
        transition: all 0.2s ease;
        white-space: nowrap;

        &:hover {
          background: #5a32a3;
        }

        &.active {
          background: #495057;

          &:hover {
            background: #343a40;
          }
        }
      }

      .error {
        color: #dc3545;
        font-size: 12px;
        text-align: center;
        padding: 8px;
        background: #f8d7da;
        border: 1px solid #f5c6cb;
        border-radius: 4px;
      }

      .audioInfo {
        text-align: center;
        padding: 8px;
        
        .audioStatus {
          display: inline-block;
          padding: 4px 8px;
          font-size: 11px;
          font-weight: 500;
          border-radius: 4px;
          background: rgba(255, 255, 255, 0.1);
          color: #fff;
          border: 1px solid rgba(255, 255, 255, 0.2);
        }
      }
    }
  }

  // 设置面板样式
  .settingsPanel {
    position: absolute;
    top: 20px;
    left: 20px;
    background: rgba(0, 0, 0, 0.9);
    padding: 20px;
    border-radius: 12px;
    backdrop-filter: blur(15px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    min-width: 280px;
    z-index: 1000;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);

    .settingsContent {
      h3 {
        color: white;
        margin: 0 0 16px 0;
        font-size: 16px;
        font-weight: 600;
        text-align: center;
      }

      .settingItem {
        margin-bottom: 16px;

        label {
          display: block;
          color: white;
          font-size: 14px;
          margin-bottom: 8px;
          font-weight: 500;
        }

        .slider {
          width: 100%;
          height: 6px;
          border-radius: 3px;
          background: #495057;
          outline: none;
          -webkit-appearance: none;
          appearance: none;

          &::-webkit-slider-thumb {
            -webkit-appearance: none;
            appearance: none;
            width: 18px;
            height: 18px;
            border-radius: 50%;
            background: #6f42c1;
            cursor: pointer;
            box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
          }

          &::-moz-range-thumb {
            width: 18px;
            height: 18px;
            border-radius: 50%;
            background: #6f42c1;
            cursor: pointer;
            border: none;
            box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
          }
        }

        .presetBtn {
          padding: 6px 12px;
          background: #495057;
          color: white;
          border: none;
          border-radius: 4px;
          cursor: pointer;
          font-size: 12px;
          margin-right: 8px;
          margin-bottom: 4px;
          transition: all 0.2s ease;

          &:hover {
            background: #6f42c1;
          }

          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }

  // 全屏样式
  &.fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: #000;
    z-index: 9999;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .canvas {
      border: none;
      margin-bottom: 0;
      flex: 1;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      canvas {
        max-width: 100%;
        max-height: 100%;
        width: 100vw !important;
        height: 100vh !important;
      }
    }

    .controls {
      position: absolute;
      top: 20px;
      right: 20px;
      background: rgba(0, 0, 0, 0.8);
      padding: 12px;
      border-radius: 8px;
      backdrop-filter: blur(10px);
      border: 1px solid rgba(255, 255, 255, 0.1);
      min-width: 200px;

      .microphoneControls {
        flex-direction: column;
        gap: 8px;

        .microphoneBtn,
        .fullscreenBtn,
        .trueFullscreenBtn {
          padding: 8px 16px;
          font-size: 12px;
          min-width: auto;
          white-space: nowrap;
        }
      }
    }
  }

  // 真全屏样式
  &:fullscreen {
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    width: 100vw !important;
    height: 100vh !important;
    background: #000 !important;
    z-index: 9999 !important;
    display: flex !important;
    flex-direction: column !important;
    justify-content: center !important;
    align-items: center !important;

    .canvas {
      border: none !important;
      margin-bottom: 0 !important;
      flex: 1 !important;
      width: 100% !important;
      height: 100% !important;
      display: flex !important;
      justify-content: center !important;
      align-items: center !important;

      canvas {
        max-width: 100% !important;
        max-height: 100% !important;
        width: 100vw !important;
        height: 100vh !important;
      }
    }

    .controls {
      position: absolute !important;
      top: 20px !important;
      right: 20px !important;
      background: rgba(0, 0, 0, 0.8) !important;
      padding: 12px !important;
      border-radius: 8px !important;
      backdrop-filter: blur(10px) !important;
      border: 1px solid rgba(255, 255, 255, 0.1) !important;
      min-width: 200px !important;

      .microphoneControls {
        flex-direction: column !important;
        gap: 8px !important;

        .microphoneBtn,
        .fullscreenBtn,
        .trueFullscreenBtn {
          padding: 8px 16px !important;
          font-size: 12px !important;
          min-width: auto !important;
          white-space: nowrap !important;
        }
      }
    }
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
